me=\"client' +\n              i +\n              '\" value=\"' +\n              row. +\n              '\">' +\n              '<div class=\"text-nowrap CustomScrollbar\"' +\n              ' style=\"overflow-x: auto; width: 100%;\">' +\n              row. +\n              \"</div>\"\n            );\n          },\n        },\n        {\n          label: app.appContext.config.globalProperties.$t(\n            \"oboundpageLang.bound\"\n          ),\n          field: \"\",\n          width: \"12ch\",\n          sortable: true,\n          display: function (row, i) {\n            return (\n              '<input type=\"hidden\" id=\"bound' +\n              i +\n              '\" name=\"bound' +\n              i +\n              '\" value=\"' +\n              row. +\n              '\">' +\n              '<div class=\"text-nowrap CustomScrollbar\"' +\n              ' style=\"overflow-x: auto; width: 100%;\">' +\n              row. +\n              \"</div>\"\n            );\n          },\n        },\n        {\n          label: app.appContext.config.globalProperties.$t(\n            \"oboundpageLang.inboundnum\"\n          ),\n          field: \"\",\n          width: \"14ch\",\n          sortable: true,\n          display: function (row, i) {\n            return (\n              '<input type=\"hidden\" id=\"inboundnum' +\n              i +\n              '\" name=\"inboundnum' +\n              i +\n              '\" value=\"' +\n              row. +\n              '\">' +\n              '<div class=\"CustomScrollbar text-nowrap\"' +\n              ' style=\"overflow-x: auto; width: 100%;\">' +\n              row. +\n              \"</div>\"\n            );\n          },\n        },\n        {\n          label: app.appContext.config.globalProperties.$t(\n            \"oboundpageLang.inpeople\"\n          ),\n          field: \"\",\n          width: \"14ch\",\n          sortable: true,\n          display: function (row, i) {\n            return (\n              '<input type=\"hidden\" id=\"inboundpeople' +\n              i +\n              '\" name=\"inboundpeople' +\n              i +\n              '\" value=\"' +\n              row. +\n              '\">' +\n              '<div class=\"text-nowrap CustomScrollbar\"' +\n              ' style=\"overflow-x: auto; width: 100%;\">' +\n              row. +\n              \"</div>\"\n            );\n          },\n        },\n        {\n          label: app.appContext.config.globalProperties.$t(\n            \"oboundpageLang.inboundtime\"\n          ),\n          field: \"\",\n          width: \"12ch\",\n          sortable: true,\n          display: function (row, i) {\n            return (\n              '<input type=\"hidden\" id=\"inboundtime' +\n              i +\n              '\" name=\"inboundtime' +\n              i +\n              '\" value=\"' +\n              row. +\n              '\">' +\n              '<div class=\"text-nowrap CustomScrollbar\"' +\n              ' style=\"overflow-x: auto; width: 100%;\">' +\n              row. +\n              \"</div>\"\n            );\n          },\n        },\n        {\n          label: app.appContext.config.globalProperties.$t(\n            \"oboundpageLang.mark\"\n          ),\n          field: \"\",\n          width: \"10ch\",\n          sortable: true,\n          display: function (row, i) {\n            if (row. === null) row. = \"\";\n            return (\n              '<input type=\"hidden\" id=\"remark' +\n              i +\n              '\" name=\"remark' +\n              i +\n              '\" value=\"' +\n              row. +\n              '\">' +\n              '<div class=\"text-nowrap CustomScrollbar\"' +\n              ' style=\"overflow-x: auto; width: 100%;\">' +\n              row. +\n              \"</div>\"\n            );\n          },\n        },\n        {\n          label: app.appContext.config.globalProperties.$t(\n            \"oboundpageLang.inreason\"\n          ),\n          field: \"\",\n          width: \"15ch\",\n          sortable: true,\n          display: function (row, i) {\n            return (\n              '<input type=\"hidden\" id=\"inboundreason' +\n              i +\n              '\" name=\"inboundreason' +\n              i +\n              '\" value=\"' +\n              row. +\n              '\">' +\n              '<div class=\"text-nowrap CustomScrollbar\"' +\n              ' style=\"overflow-x: auto; width: 100%;\">' +\n              row. +\n              \"</div>\"\n            );\n          },\n        },\n      ],\n      rows: computed(() => {\n        return data.filter((x) =>\n          x.\n            .toLowerCase()\n            .includes(searchTerm.value.toLowerCase()) ||\n          x.\n            .includes(searchTerm.value)\n        );\n      }),\n      totalRecordCount: computed(() => {\n        return table.rows.length;\n      }),\n      sortable: {\n        order: \"id\",\n        sort: \"asc\",\n      },\n      messages: {\n        pagingInfo:\n          app.appContext.config.globalProperties.$t(\n            \"basicInfoLang.now_showing\"\n          ) +\n          \" {0} ~ {1} \" +\n          app.appContext.config.globalProperties.$t(\"basicInfoLang.record\") +\n          \", \" +\n          app.appContext.config.globalProperties.$t(\"basicInfoLang.total\") +\n          \" {2} \" +\n          app.appContext.config.globalProperties.$t(\"basicInfoLang.record\"),\n        pageSizeChangeLabel: app.appContext.config.globalProperties.$t(\n          \"basicInfoLang.records_per_page\"\n        ),\n        gotoPageLabel: app.appContext.config.globalProperties.$t(\n          \"basicInfoLang.go_to_page\"\n        ),\n        noDataAvailable: app.appContext.config.globalProperties.$t(\n          \"basicInfoLang.search_with_no_data_returned\"\n        ),\n      },\n      pageOptions: [\n        {\n          value: 10,\n          text: 10,\n        },\n        {\n          value: 20,\n          text: 20,\n        },\n        {\n          value: 40,\n          text: 40,\n        },\n        {\n          value: 100,\n          text: 100,\n        },\n      ],\n    });\n\n    const updateCheckedRows = (rowsKey) => {\n      console.log(rowsKey)\n      // only check one\n      let count = ($('.vtl-paging-count-dropdown').val());\n      rowsKey = parseInt(rowsKey);\n      for (let i = 0; i < count; i++) {\n        if (i !== rowsKey) {\n          $(\"#checkbox\" + i).prop(\"disabled\", true);\n        }\n        else {\n          $(\"#checkbox\" + i).prop(\"disabled\", false);\n        }\n      };\n      if (isNaN(rowsKey)) {\n        console.log(1);\n        $('.vtl-tbody-checkbox').prop(\"disabled\", false);\n      }\n    }\n    return {\n      searchTerm,\n      table,\n      updateCheckedRows,\n    };\n  }, // setup\n});\n</script>\n<style scoped>\n::v-deep(.vtl-table .vtl-thead .vtl-thead-th) {\n  color: #fff;\n  background-color: #196241;\n  border-color: #196241;\n}\n</style>"],"sourceRoot":""}]);
